<template>
  <div id="app">
    <demonstration
        tag="h2"
        title="Icon "
        anchor="icon-tu-biao"
        description=""
        :show-content="false">
    </demonstration>
    <demonstration
        title=""
        anchor="gai-shu"
        :show-content="false">
      <template slot="description">
         <a href="http://ionicons.com/" target="_blank">ionicons</a>
      </template>
    </demonstration>
    <demonstration
        title=""
        anchor="shi-yong-fang-fa"
        :multiple="true"
        :highlight="sourcecode">
      <template slot="description">
         <code>type</code> 
      </template>
      <template slot="source">
        <div class="vd-demo__block vd-demo__block-1">
          <span class="vd-demo__demonstration">heart</span>
          <div>
            <vm-icon type="heart"></vm-icon>
          </div>
        </div>
        <div class="vd-demo__block vd-demo__block-1">
          <span class="vd-demo__demonstration">#FF4949</span>
          <div>
            <vm-icon type="heart" color="#FF4949"></vm-icon>
          </div>
        </div>
        <div class="vd-demo__block vd-demo__block-1">
          <span class="vd-demo__demonstration">px</span>
          <div>
            <vm-icon type="heart" :size="30"></vm-icon>
          </div>
        </div>
        <div class="vd-demo__block vd-demo__block-1">
          <span class="vd-demo__demonstration">em</span>
          <div>
            <vm-icon type="heart" size="2em"></vm-icon>
          </div>
        </div>
      </template>
      <template slot="explanation">
         <code>type</code>  <code>i class="vm-icon-heart"</code> <code>size</code>
         <code>color</code>  <code>size</code>  <code>color</code>
        style  <code>style="font-size: 2em; color: red;"</code>
      </template>
    </demonstration>
    <demonstration
        title="Attributes"
        anchor="attributes"
        :show-content="false">
      <template slot="table">
        <table class="vd-demo__table">
          <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          </thead>
          <tbody>
          <tr>
            <td>tag</td>
            <td></td>
            <td>string</td>
            <td>*</td>
            <td>i</td>
          </tr>
          <tr>
            <td>type</td>
            <td></td>
            <td>string</td>
            <td></td>
            <td></td>
          </tr>
          <tr>
            <td>size</td>
            <td> %, in, cm, mm, em, ex, pt, pc, px   px</td>
            <td>number/string</td>
            <td></td>
            <td></td>
          </tr>
          <tr>
            <td>color</td>
            <td></td>
            <td>String</td>
            <td></td>
            <td></td>
          </tr>
          </tbody>
        </table>
      </template>
    </demonstration>
    <demonstration
        title=""
        anchor="tu-biao-ji-he"
        :show-content="false">
      <template>
        <div class="icon-search">
          <input type="text" v-model="keyword" placeholder=" heart">
          <p></p>
        </div>
        <div class="icons">
          <div class="icons-item"
               v-for="(icon, index) in icons"
               @click="handleClickIcon(icon, index, $event)"
               @mouseleave="handleMouseleave()"
               @mousemove="handleMousemove(icon, index)">
            <i :class="'vm-icon-'+icon.name" style="font-size: 32px;"></i>
            <p>{{icon.name}}</p>
          </div>
          <transition name="copy">
            <div class="copied" v-show="showCopy" :style="{ top, left }">Copied!</div>
          </transition>
        </div>
      </template>
    </demonstration>
  </div>
</template>
<script>
  import { sourcecode } from './template'
  import { icons } from './icons.json'
  import Clipboard from 'clipboard'
  export default {
    data () {
      return {
        sourcecode,
        keyword: '',
        showCopy: false,
        top: 0,
        left: 0
      }
    },
    computed: {
      icons () {
        return icons.filter(icon => {
          return icon.name.indexOf(this.keyword) > -1
        })
      }
    },
    methods: {
      handleClickIcon (icon, index, event) {
        var clip = new Clipboard('.icons', {
          text () {
            return `<vm-icon type="${icon.name}"></vm-icon>`
          }
        })

        clip.on('success', e => {
          e.clearSelection();
          clip.destroy();
          this.left = index % 5 * 200 + 70 + 'px'
          this.top = (Math.floor(index / 5) + 1) * 100 + 'px'
          this.showCopy = true
        })
      },
      handleMousemove (icon, index) {
      },
      handleMouseleave () {
        this.showCopy = false
      }
    }
  }
</script>

<style lang="less" rel="stylesheet/less">
  #app {
    margin: 60px auto;
    .icon-search {
      margin: 20px auto 30px;
      text-align: center;
      input {
        width: 500px;
        margin: 0 auto;
        padding: 8px 0;
        font-size: 14px;
        text-align: center;
        color: #5e6d82;
        background-color: #eff2f7;
        border: 0;
        border-radius: 4px;
        outline: none;
      }
      p {
        margin-top: 20px;
      }
    }
    .icons {
      position: relative;
      overflow: hidden;
      padding-bottom: 50px;
      zoom: 1;
      .copy-enter-active,
      .copy-leave-active {
        transition: opacity .5s
      }
      .copy-enter, .copy-leave-to {
        opacity: 0
      }
      .copied {
        position: absolute;
        left: 0;
        top: 30px;
        z-index: 99;
        padding: 5px 8px;
        font: normal normal 11px/1.5 Helvetica, arial, nimbussansl, liberationsans, freesans, clean, sans-serif, "Segoe UI Emoji", "Segoe UI Symbol";
        color: #fff;
        text-align: center;
        text-decoration: none;
        text-shadow: none;
        text-transform: none;
        letter-spacing: normal;
        -ms-word-wrap: break-word;
        word-wrap: break-word;
        white-space: pre;
        pointer-events: none;
        background: rgba(0, 0, 0, 0.8);
        border-radius: 3px;
        -webkit-font-smoothing: subpixel-antialiased;
        &:before {
          position: absolute;
          top: -10px;
          left: 25px;
          width: 0;
          height: 0;
          color: rgba(0, 0, 0, 0.8);
          pointer-events: none;
          content: "";
          border: 5px solid transparent;
          border-bottom-color: rgba(0, 0, 0, 0.8);
        }
      }
      .icons-item {
        float: left;
        width: 200px;
        height: 90px;
        padding-top: 10px;
        color: #5c6b77;
        text-align: center;
        cursor: pointer;
        p {
          margin: 5px;
          padding-top: 15px;
          font-size: 14px;
        }
      }
    }
  }
</style>



